import { Component, OnInit } from '@angular/core';
import { createAnimation } from '@ionic/angular';
import { PlanServiceService } from '../plan-locaux/plan-service/plan-service.service';


@Component({
  selector: 'app-transport-datail',
  templateUrl: './transport-datail.page.html',
  styleUrls: ['./transport-datail.page.scss'],
})
export class TransportDatailPage implements OnInit {

  lat: any;
  mylong: any;
  long:any;
  zoom: any;
  planData: any = [];

  constructor(private planService: PlanServiceService,) { }

  ngOnInit() {
    this.getPosition()
    //this.center = {lat: plan.location.lat, lng: plan.location.lng};
    this.zoom = 12;
  }

  searchPlan(currentplan) {
    console.log('donées dans console', currentplan);
    this.planService.googleInit(currentplan.name, this.lat, this.long).subscribe(data => {
      // this.getallLocations(data);
      console.log('donées',data);

      this.planData = data;
      this.planData.results.map(plan => {
        const planObject = {
          location: plan.geometry.location,
          name: plan.name,
          openingHours: plan.opening_hours,
          adresse: plan.vicinity,
          rating: plan.rating,
          userRatingTotal: plan.user_ratings_total,
          icon: currentplan.icon
        };
      });
    });
  }


  getPosition(){
    navigator.geolocation.getCurrentPosition(resp =>{
      this.lat = resp.coords.latitude;
      this.long =  resp.coords.longitude;

    })

  }

  openBottomSheet(){
    (<HTMLStyleElement>document.querySelector("#transportcartoBottomSheet")).style.display ="block";
    const animation = createAnimation()
      .addElement(document.querySelector("#transportcartoBottomSheet"))
      .easing("ease-in-out")
      .duration(400)
      .direction("alternate")
      .keyframes([
        { height: "0px", transform: "scale(1)", opacity: "1",},
        { height: "26rem", transform: "scale(1)", opacity: "1" },
      ]);
    animation.play();
  }

  closeBottomSheet(){
    const animation = createAnimation()
    .addElement(document.querySelector("#transportcartoBottomSheet"))
    .easing("ease-in-out")
    .duration(300)
    .direction("alternate")
    .keyframes([
      { height: "26rem",},
      { height:  "0px", opacity: "0", },
    ]);
    animation.play();
  }


}
